.image-size {
    height: 350px;
    width: auto;
}

@media (max-width: 1000px) {
    .image-size {
        height: 250px;
        width: auto;
    }
}

.pointer {
    cursor: pointer;
}

.f-1-5 {
    font-size: 1.5rem;
}

.f-1-3 {
    font-size: 1.3rem;
}

.float-right {
    float: right;
}

.transparent-btn {
    background-color: transparent !important;
    color: black !important;
}

.border-none {
    border-color: transparent !important;
}

.clr-black {
    color: black;
}

.border-btm {
    border-bottom: 1px solid #c5bbbb;
}

.comment-div {
    border: 1px solid #d1d1d2;
    padding-left: 24px;
    padding-right: 24px;
    background-color: #f2f3f5;
    border-radius: 27px;
}

.comment-section {
    height: 56vh;
    overflow-y: scroll;
}
.full-width{
	width: 100%
}

.container-image {
	height: 80vh;
}
.mt-90 {
    margin-top: 90px;
}
#mainNav {
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-weight: 700;
    font-family: Montserrat;
}

#mainNav .navbar-brand {
    color: #fff;
}

.navbar-brand {
    display: inline-block;
    padding-top: .3125rem;
    padding-bottom: .3125rem;
    margin-right: 1rem;
    font-size: 1.25rem;
    line-height: inherit;
    white-space: nowrap;
}

.bg-secondary {
    background-color: #2c3e50!important;
}

.fixed-top {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
}

.copyright {
    background-color: #1a252f;
}

.text-white {
    color: #fff!important;
}
