.image-size {
    height: 350px;
    width: auto;
}

@media (max-width: 1000px) {
    .image-size {
        height: 250px;
        width: auto;
    }
}

.pointer {
    cursor: pointer;
}

.f-1-5 {
    font-size: 1.5rem;
}

.f-1-3 {
    font-size: 1.3rem;
}

.float-right {
    float: right;
}

.transparent-btn {
    background-color: transparent !important;
    color: black !important;
}

.border-none {
    border-color: transparent !important;
}

.clr-black {
    color: black;
}

.border-btm {
    border-bottom: 1px solid #c5bbbb;
}

.comment-div {
    border: 1px solid #d1d1d2;
    padding-left: 24px;
    padding-right: 24px;
    background-color: #f2f3f5;
    border-radius: 27px;
}

.comment-section {
    height: 56vh;
    overflow-y: scroll;
}